<template>
	<view>
		<view class="tab-item" @tap='tab_click'>
			{{name}}
		</view>
		<slot>
		  <p>如果父组件没用插入内容,我将作为默认出现</p>
		</slot>
	</view>
</template>

<script>
	export default {
		props:{
			name:String
		},
		data() {
			return {
			};
		},
		watch:{
			searchKey(val){
				let keywords = this.searchKey;
				this.amapPlugin = new amap.AMapWX({
					key: this.key  
				});  
				this.amapPlugin.getInputtips({
							keywords: keywords,
							city: this.cityCode,
							success: (data) => {  
								if(data && data.tips){
								   this.searchList = data.tips;
							   }
							}  
					});  
			}
		},
		methods:{
			tab_click(){
				this.$emit('showtabStr',{id:'001',name:'约车'})
			}
		}
	}
</script>

<style lang="less" scoped rel="stylesheet/less">
	.tab-item{
		padding-top: 80px;
		width: 50px;
		height: 40px;
	}
</style>
